import React,{Component} from 'react';
import {BrowserRouter as Router,Route, Link} from "react-router-dom";

import axios from 'axios';





class Members extends Component{


	constructor(props){
		  super(props)
		  this.state ={
		    members :[],
		    first_name:'',
		    last_name:'',
		    email:'',
		    buttonDisabled : false,
		    formStatus : 'Create',
		    memberIdSelected : null,
		    avatar : ''
		  }
		}



componentDidMount(){
  axios.get('https://reqres.in/api/users?page=1')
        .then(response=>{
          console.log('response', response)
          this.setState({members:response.data.data})
        })
        .catch(error=>{
          console.log('error',error)
        })
}

inputOnChangeHandler = (event)=>{
  this.setState({[event.target.name]:event.target.value})
}

onSubmitHandler = (e)=>{
  console.log('Siap Submit')
  e.preventDefault()
  this.setState({buttonDisabled:true})
  var payload ={
    first_name: this.state.first_name,
    last_name: this.state.last_name,
    email: this.state.email
  }
  var url ='https://reqres.in/api/users'
  if (this.state.formStatus==='Create'){
      this.addMember(url,payload)
  } else {
    url = 'https://reqres.in/api/users/${this.state.memberIdSelected}'
    this.editMember(url,payload)
  }

 
}

addMember =(url,payload)=>{
   axios.post(url,payload)
        .then(response=>{
          console.log('berhasil post')

          var members=[...this.state.members]
          members.push(response.data)
          this.setState({members, buttonDisabled:false, first_name:'',last_name:'', email:''})
        })
        .catch(error=>{
          console.log(error)
        })
}

editMember =(url,payload)=>{
   axios.put(url,payload)
        .then(response=>{
          var members=[...this.state.members]
          var indexMember = members.findIndex(member=>member.id===this.state.memberIdSelected)


          members[indexMember].first_name=response.data.first_name
          members[indexMember].last_name=response.data.last_name
          members[indexMember].email=response.data.email
          this.setState({
            members,
            buttonDisabled:false, 
            first_name:'',
            last_name:'',
            email:'',
            formStatus:'Create'
          })
        })
        .catch(error=>{
          console.log(error)
        })
}

deleteButtonHandler =(id)=>{
  var url = 'https://reqres.in/api/users/${id}'
  axios.delete(url)
    .then(response=>{
        if(response.status===204){
          var members=[...this.state.members]
          var index = members.findIndex(member=>member.id===id)
          members.splice(index,1)
          this.setState({members})
        }
    })
    .catch(error=>{
      console.log(error)
    })
}








editButtonHandler = (member)=>{
  this.setState({
    first_name : member.first_name,
    last_name : member.last_name,
    email : member.email,
    formStatus : 'Edit',
    memberIdSelected : member.id
  })
}



	render(){
		return(

<main role="main" className="container-fluid text-justify">
<br/>
<br/>
<br/><br/>
<br/>
<br/><br/>
<br/>
<br/>
<div className="container">
<div className="row">

          <div className="col-md">
            <h2 className="text-center">Form {this.state.formStatus}</h2>
              <div className="container">

               

                <form onSubmit={this.onSubmitHandler}>
                  <div className="form-group">
                    <label>First Name</label>
                    <input type="text" className="form-control" name="first_name" value={this.state.first_name} onChange={this.inputOnChangeHandler}/>
                  </div>

                  <div className="form-group">
                    <label>Last Name</label>
                    <input type="text" className="form-control" name="last_name" value={this.state.last_name} onChange={this.inputOnChangeHandler}/>
                  </div>



                  <div className="form-group">
                    <label>Email</label>
                    <input type="text" className="form-control" name="email" value={this.state.email} onChange={this.inputOnChangeHandler} placeholder="We Will Not Share Your Email"/>
                  </div>


                  <div className='form-group'>

                    <label> Keterangan :</label>
                        <textarea
                          className="form-control"
                          name="keterangan"
                        />
                    </div>

                  <button type="submit" className="btn btn-warning" disabled={this.state.buttonDisabled}>Submit</button>
                    


                </form>


              </div>
          </div>
         </div>







<br/>
<br/>
<br/><br/>
<br/>
<br/>
<br/>


         <div className="row">
          <div className="col-md" >
              <h2 className="text-center">Member</h2>
              <br/>
              <br/>
              <div className="row">


            

 {this.state.members.map((member)=>(
                <div className="col-md" key={member.id}>
                  <div className="card" style={{margin:10}}>
                    <img className="card-img-top" src={member.avatar} alt="Card image cap"/>
                    <div className="card-body">
                      <h5 className="card-title">{member.first_name}</h5>
                      <h5 className="card-title">{member.last_name}</h5>
                      <h5 className="card-title">{member.email}</h5>
                      <button className="btn btn-primary" onClick={()=>this.editButtonHandler(member)}>Edit</button> &nbsp;
                      <button className="btn btn-danger" onClick={()=>this.deleteButtonHandler(member.id)}>Delete</button>
                    </div>
                  </div>
                </div>
 ))}
                



              </div>


          </div>
          </div>
          

      </div>
	  <br/>
      <br/>
      <br/>
      <br/>
      <br/>
      <br/>
      <br/> <br/>
      <br/> <br/>
      <br/>
  
      </main>

	)}}
export default Members;