import React,{Component} from 'react';
import {BrowserRouter as Router,Route, Link} from "react-router-dom";

import axios from 'axios';





class Zodiak extends Component{


	constructor(props){
		  super(props)
		  this.state ={
		    members :{},
		    nama:'Wahangga',
		    tanggal_lahir:'10-08-1990',
		    email:'',
		    buttonDisabled : false,
		    formStatus : 'Create',
		    memberIdSelected : null,
		    avatar : '',

		  }
		}



componentDidMount(){
  axios.get(`https://script.google.com/macros/exec?service=AKfycbw7gKzP-WYV2F5mc9RaR7yE3Ve1yN91Tjs91hp_jHSE02dSv9w&nama=${this.state.nama}&tanggal=${this.state.tanggal_lahir}`)
        .then(response=>{
          console.log('response', response)
          this.setState({members:response.data.data})
        })
        .catch(error=>{
          console.log('error',error)
        })
}

inputOnChangeHandler = (event)=>{
  this.setState({[event.target.name]:event.target.value})
}

onClickZodiakHandler =(event)=>{
  this.setState({tanggal_lahir:'27-03-1993'})
}










	render(){
		return(

<main role="main" className="container-fluid text-justify">
<br/>

<br/><br/>
<br/>
<br/>
<div className="container">




         <div className="row">
          <div className="col-md" >
          <button className="button btn-lg btn-success" onClick={this.onClickZodiakHandler}>Zodiakmu</button>
              <br/>
              <br/>
              <div className="row">


            
			<p>{this.state.members.zodiak}   </p>


                



              </div>


          </div>
          </div>
          

      </div>
	  <br/>
      <br/>
      <br/>
      <br/>
      <br/>
      <br/>
      <br/> <br/>
      <br/> <br/>
      <br/>
  
      </main>

	)}}
export default Zodiak;